@import url(https://fonts.googleapis.com/css2?family=Ubuntu+Condensed&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Yanone+Kaffeesatz:wght@400;500;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Oswald&display=swap);
/* content */


html {
    box-sizing: border-box;
    font-size: 62.5%; /* 1rem = 10px, 1.6rem = 16px ...*/
}

* {
    box-sizing: inherit;
    margin: 0;
    padding: 0;
}

body {
    font-size: 1.6rem;
    font-family: 'Ubuntu Condensed', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

/* --- Login --- */
.btn-login {
    font-size: 0.9rem;
    letter-spacing: 0.05rem;
    padding: 0.75rem 1rem;
}

.btn-google {
    color: white !important;
    background-color: #ea4335;
}

.btn-facebook {
    color: white !important;
    background-color: #3b5998;
}

.enlace-cuenta {
    margin-top: 2rem;
    display: block;
    opacity: .7;
}

/* --- dashboar --- */
.sidebar{
    width: 18rem;
    height: calc(100vh - 52px);
    border-right: 1px solid gray;
}

.sidebar ul {
    list-style: none;
    padding: 1rem 2rem;
}

.sidebar ul li{
    margin-bottom: 1rem;
}

.sidebar ul li a{
    text-decoration: none;
}

/* .active{
    Si se va a utilizar, usar de formas mas especifica
    o afectara a todos los enlaces
    background-color: #DAD8D8;
} */

.flex {
    display: flex;
}

.content {
    width: 100%;
}


/* titles */

:root {
    --white: #fff;
    --orange: #f73600;
    --orange-dark: #cc2b2b;
    --vinotinto: #310101;
}

.App {
    width: 100%;
    height: 100vh;
    background: #fff;
}

img {
    display: block;
    max-width: 100%;
    height: auto;
}

a {
    text-decoration: none;
}

/* Container */
.wrapper-xxl {
    width: 100%;
}

.wrapper {
    max-width: 95%;
    margin: 0 auto;
    padding: 40px 20px;
}

/* Titles */
.titles {
    font: bold 5vh 'Yanone Kaffeesatz', sans-serif;
}
.section--title {
    margin: 20px auto 40px;
    font-weight: 500;
}
.page--title {
    margin: 40px auto;
    color: #310101;
    color: var(--vinotinto);
    font-size: 8vh;
}


/* Carrousel: Slides */
.slide {
    position: relative;
    min-width: 100%;
    max-height: -webkit-fit-content;
    max-height: -moz-fit-content;
    max-height: fit-content;
    overflow: hidden;
    transition: .3s ease all;
    z-index: 10;
}
.slide .wrapper {
    padding: 0 40px;
}

.slide img {
    vertical-align: top;
}


/* Media Queries */
@media (min-width: 1200px) {
    .wrapper {
        max-width: 80%;
    }
}
/* Slider */
.carousel-img {
    object-fit: cover;
    height: 60vh;
    width: 100%;
    pointer-events: none;
}

.carousel-caption{
    position: absolute;
    top: 10%;
    right: 0;
    left: 0;
    bottom: 0;
    text-align: center;
    pointer-events: none;
}

.carousel-caption img {
    max-width: 270px;
    margin: 0 auto;
}

.carousel-caption p.section--title {
    max-width: 95%;
    margin: 30px auto 0;
    color: #fff;
    text-shadow: 1px 1px 10px #310101, 0 0 1em #f73600, 0 0 0.2em #cc2b2b;
}



@media (min-width: 510px) {
    .carousel-caption img {
        max-width: 300px;
    }
    .carousel-caption p.section--title {
        margin-top: 3vh;
    }
}

@media (min-width: 653px) {
    .carousel-caption p.section--title {
        font-size: 7vh;
    }
}

@media (min-width: 1020px) {
    .carousel-caption img {
        max-width: 320px;
    }
    .carousel-caption p.section--title {
        margin-top: 5vh;
    }
}
.testimonial {
    display: flex;
    justify-content: center;
    align-items: center;
    max-width: 80%;
    margin: 5% auto;
    text-align: center;
}

.testimonial-data {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-family: 'Oswald', sans-serif;
    color: #310101;
}

.testimonial-data img {
    width: 120px;
    height: 120px;
    margin-bottom: 3%;
    border: 7px double rgba(0, 0, 0, 0.2);
    border-radius: 50%;
}

.testimonial-data .detail {
    position: relative;
    margin-bottom: 20px;
    font-size: 1.6rem;
}

.testimonial-data .client {
    margin-bottom: 0;
    font-size: 1.2rem;
}

.icon-left,
.icon-right {
    position: absolute;
    font-size: 3.3rem;
    opacity: 0.1;
}

.icon-left {
    margin-top: -20px;
    left: 0;
}

.icon-right {
    margin: 5px;
}
.events-section {
    background: linear-gradient(-180deg, var(--orange-dark) 30%, var(--orange) 50%);
    background-image: url("https://i.postimg.cc/gJ7XT57Y/foto3-2x.png");
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    color: var(--white);
}

.wrapper-cards {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(300px, 320px));
    justify-content: space-evenly;
    align-items: center;
    grid-gap: 15px;
    gap: 15px;
    margin-top: 70px;
}

.card {
    display: flex;
    flex-direction: column;
    min-width: 0;
    max-width: 320px;
    height: auto;
    padding-bottom: 15px;
    background-color: transparent;
    border: none;
    border-radius: 4px;
    text-align: center;
    word-wrap: break-word;
}

/* Header */
.card header img {
    object-fit: contain;
    width: 100%;
    height: 180px;
}

/* Body */
.card--title {
    padding: 10px 0 0;
    margin-bottom: 1rem;
    font-size: 2.4rem;
    font-weight: bold;
}

.card-details {
    width: 100%;
    padding: 10px 15px;
    margin-bottom: 1rem;
    font-size: 1.4rem;
    text-align: justify;
}

/* Footer */
.card footer {
    padding: 10px;
}

.icon {
    display: inline-block;
    width: 40px;
    height: 40px;
    padding: 5px;
    color: inherit;
    opacity: .7;
    background-color: rgba(0, 0, 0, 0.9);
    border-radius: 50%;
    transition: all .5s ease;
}
.icon svg {
    width: 30px;
    height: 30px;
}

.icon:hover {
    opacity: 1;
    color: var(--orange-dark);
}
.recommendations-section {
    width: 100%;
    min-height: 600px;
}

.recommendations-section h2 {
    font-size: 4vh;
    color: var(--vinotinto);
}

/* Containers */
.trending-products {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
}

.products {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
    justify-items: center;
    align-items: center;
    grid-gap: 15px;
    gap: 15px;
    place-items: center;
}

/* Plates */
.plate-image {
    position: relative;
    border-radius: 8px;
    box-shadow: 2px 2px 2px 1px rgba(0, 0, 0, 0.2);;
    transition: -webkit-transform 0.4s ease-in-out;;
    transition: transform 0.4s ease-in-out;;
    transition: transform 0.4s ease-in-out, -webkit-transform 0.4s ease-in-out;
    overflow: hidden;
}

.plate-image:hover {
    -webkit-transform: scale(1.05) translateY(-8px);
            transform: scale(1.05) translateY(-8px);
    box-shadow: 2px 2px 10px rgba(0, 0, 0, 0.5);
}

.plate-details {
    position: absolute;
    top: 83%;
    right: 0;
    bottom: 0;
    left: 0;
    padding: 10px;
    background: rgba(0, 0, 0, 0.5);
    font-weight: 400;
    transition: top 0.6s ease-out 0.05s;
}

.plate-image:hover .plate-details {
    top: 0;
}

.plate-name {
    font-size: 2rem;
    color: var(--white);
}

.plate-description {
    padding-top: 7px;
    font-size: 1.2rem;
    line-height: 1.6rem;
    color: var(--white);
}

/* Imagen and Button */
.menu-link img {
    display: none;
}

.menu-link a {
    margin-top: 20px;
    font-size: 1.6rem;
    background-color: var(--orange);
    color: var(--white);
}
.menu-link a:hover {
    background-color: var(--orange-dark);
}

.menu-link--button {
    display: block;
    max-width: 100%;
    padding: 10px;
    border-radius: 8px;
}


/* --- Media Queries --- */
@media (min-width: 410px) {
    .plate-details {
        top: 85%;
        padding-top: 5px;
    }
    .plate-image:hover .plate-details{
        padding-top: 15px;
    }
    .plate-name {
        padding-top: 5px;
        font-size: 2.2rem;
    }
    .plate-description {
        font-size: 1.4rem;
        letter-spacing: 0.01em;
    }
}

@media (min-width: 510px) {
    .plate-details {
        top: 87%;
    }
    .plate-name {
        font-size: 2.4rem;
    }
    .plate-description {
        font-size: 1.6rem;
    }
}

@media (min-width: 653px) {
    .plate-details {
        top: 85%;
    }
    .plate-name {
        font-size: 2rem;
    }
    .plate-description {
        font-size: 1.2rem;
    }
}

@media (min-width: 1020px) {
    .trending-products {
        flex-direction: row;
    }
    .products,
    .menu-link {
        flex: 1 1;
    }
    .products {
        grid-template-columns: repeat(2, minmax(280px, 1fr));
    }
    .menu-link {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        margin-left: 3%;
    }
    .menu-link img {
        display: block;
        height: 420px;
    }
}


@media (min-width: 1200px) {
    .trending-products {
        margin-left: 5%;
    }
    .menu-link img {
        height: auto;
        max-height: 500px;
    }
}
.header,
.header.fixed {
    display: flex;
    align-items: center;
    height: 80px;
    background-color: rgba(0, 0, 0, .5);
    box-shadow: 0 .125rem .25rem rgba(0, 0, 0, .2);
    z-index: 100;
}

.header {
    position: relative;
    background-color: #000;
}

.header.fixed {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
}

.navbar-main {
    display: flex;
    align-items: center;
    justify-content: space-between;
    height: 100%;
}

/* Nav Links */
.header__options {
    flex: 1 1;
    position: absolute;
    top: 80px;
    left: -100%;
    right: 0;
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 100%;
    opacity: 0;
    background-color: rgba(0, 0, 0, .5);
    list-style-type: none;
    pointer-events: none;
    z-index: 100;
    transition: all .5s ease-in-out;
}

.header__options.active {
    left: 0;
    padding: 5px 30px;
    opacity: 1;
    pointer-events: all;
    transition: all .5s ease;
}

.header__options li a {
    display: flex;
    align-items: center;
    height: 40px;
    padding-left: 5px;
    color: #fff;
    font-size: 1.6rem;
    cursor: pointer;
}
.header__options li:not(li.header__options-sigin):hover {
    background-color: rgba(0, 0, 0, .5);
    -webkit-transform: translateY(-5px);
            transform: translateY(-5px);
    transition: all 0.5s ease-out;
}

.menu__icon {
    width: 32px;
    height: 32px;
    padding: 5px;
    fill: var(--vinotinto);
    background-color: rgba(255, 255, 255, .5);
    cursor: pointer;
}

/* SignIn Button */
.header__options-sigin {
    padding: 4px 0;
    background-color: var(--orange);
    border-radius: 8px;
    transition: all 0.3s .2s;
}
.header__options-sigin a {
    justify-content: center;
    width: 100%;
    height: 100%;
    padding-left: 0;
    font-weight: 600;
    transition: all 0.3s;
}

.header__options-sigin:hover  {
    background-color: #fff;
}
.header__options-sigin:hover a {
    color: var(--orange-dark);
}


/* Media Queries Desktop View */
@media screen and (min-width: 768px) {
    .menu__icon {
        display: none;
    }

    .navbar__logo--container {
        flex: 1 1;
    }

    .header__options {
        position: static;
        flex-direction: row;
        align-items: center;
        justify-content: flex-end;
        opacity: 1;
        white-space: nowrap;
        pointer-events: all;
        background-color: transparent;
        transition: none;
    }

    .header__options.active {
        padding: 0;
        transition: none;
    }

    .header__options li a {
        justify-content: center;
        padding: 0 5px;
    }

    .header__options-sigin {
        padding: 4px 10px;
        margin-left: 5px;
    }
}


@media screen and (min-width: 992px) {
    .header__options li a {
        padding: 0 8px;
    }
}
