.reservations-section {
    min-height: 80vh;
    width: 100%;
}


/* Swal Popup Modal */
.swal2-container.swal2-center > .swal2-popup {
    max-width: 500px;
    font-size: 2vh;
}


/* Form */
.main--form {
    flex: 1 1;
    width: 100%;
    max-width: 500px;
    margin: 0 auto;
    padding: 30px;
    color: var(--white);
    background-color: var(--vinotinto);
    border-radius: 5px;
    box-shadow: 3px 3px 10px rgba(0, 0, 0, 0.5);
}

.main--form .input-wrapper {
    min-height: 90px;
}
.main--form .input-wrapper.select {
    min-height: 70px;
}

.main--form label {
    font-size: 1.5rem;
}

.main--form input,
.main--form select,
.main--form textarea {
    display: inline-block;
    width: 100%;
    padding: 5px;
    margin-top: 5px;
    color: var(--white);
    font-size: 1.6rem;
    background-color: transparent;
    outline: none;
    border: none;
    border-bottom: 1px solid rgba(255, 255, 255, 0.3);
}

.main--form input:focus,
.main--form select:focus,
.main--form textarea:focus {
    border-bottom-color: var(--white);
}

.main--form input[type="datetime-local"]:focus,
.main--form select:focus {
    background-color: var(--orange-dark);
}

.main--form input[type="datetime-local"] {
    color: #fff;
}

.main--form select option {
    background-color: var(--vinotinto);
}

.main--form textarea {
    min-height: 120px;
    font-size: 1.4rem;
    border: 1px solid rgba(255, 255, 255, 0.3);
}

    /* Submit Button */
.main--form input[type="submit"] {
    display: block;
    width: auto;
    width: initial;
    padding: 8px 20px;
    margin: 30px auto 20px;
    border: none;
    border-radius: 5px;
    background-color: var(--orange);
    cursor: pointer;
    outline: none;
    transition: all 0.3s linear 0.1s;
}
.main--form input[type="submit"]:hover {
    font-weight: bold;
    color: var(--vinotinto);
    background-color: var(--white);
}

.main--form input[disabled] {
